@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@300;400;500;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap');
@font-face {
    font-family: 'Emilea';
    src: url('Emilea.eot');
    src: local('Emilea'),
        url('Emilea.eot?#iefix') format('embedded-opentype'),
        url('Emilea.woff2') format('woff2'),
        url('Emilea.woff') format('woff'),
        url('Emilea.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}